.spinner {
  transform-origin: 25px 25px;
  z-index: 2;
  width: 50px;
  height: 50px;
  margin: 0;
  animation: 2s linear infinite rotate;
}

.spinner .path {
  stroke: #004986;
  stroke-linecap: round;
  animation: 6s ease-in-out dash;
}

svg#countdown.paused .spinner .path, svg#countdown.paused .spinner {
  animation-play-state: paused;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1 150;
    stroke-dashoffset: 0;
  }

  100% {
    stroke-dasharray: 150 150;
    stroke-dashoffset: 0;
  }
}

svg#countdown g.pause, svg#countdown.paused g.play {
  opacity: .3;
}

